export default "<template>\r\n  <div class=\"greeting\" v-console:warn=\"'Name is ' + name\">\r\n    <h1>Hello {{ name }}!</h1>\r\n\r\n    <div>\r\n      <input style=\"border: 1px solid blue\" type=\"text\" v-model=\"name\" />\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  data() {\r\n    return {\r\n      name: 'Jane Doe'\r\n    }\r\n  }\r\n}\r\n</script>\r\n";